.dialogs {
  display: grid;
  grid-template-columns: 2fr 10fr;
}

.dialogsItems {
  padding: 15px;
  color: white;
}

.dialogsItems .active {
  color: gold;
}

.messages {
  padding: 15px;
}

.messages .message {
  color: white;
}
.form {
  padding-left: 15px;
  padding-bottom: 15px;
}
